*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body,
#root,
.container{
    height: 100%;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    /* width: 80% */
    
}

.movies .movie{
    width: 45% ;
    background-color:white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    border-radius: 10px;
    color: #b2b3bc;
    box-shadow: 0 13px 27px -5px rgb(50 50 93 / 25%), 0 8px 16px -8px rgb(0 0 0 / 30%), 0 -6px 16px -6px rgb(0 0 0 / 3%);
    padding: 20px;
}

.movie img{
    position: relative;
    top: -30px;
    max-width: 140px;
    margin-right: 30px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgb(50 50 93 / 25%), 0 8px 16px -8px rgb(0 0 0 / 30%), 0 -6px 16px -6px rgb(0 0 0 / 3%);
}

.movie .movie__title,
.movie .movie__year{
    margin: 0;
    font-weight: 300;
}

.movie .movie__title{
    color: #2c2c2c
}


.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
}

.movie .movie__genres{
    list-style-type:none;
    padding: 0;
    margin: 5px;
    display: flex;
}

.movie__genres li{
    margin-right: 10px;
    font-size: 14px;
}